'use client';
import React, { useState } from 'react';

const FirstSection: React.FC = () => {
  return (
    <div className="w-full md:w-[539px] h-full bg-[#F8F8F8] rounded-[12px] flex flex-col justify-between p-4 relative">
      {/* Top Left Icon */}
      <div className="w-[48px] h-[48px] bg-gray-300 rounded-md absolute top-4 left-4"></div>

      {/* Center Image */}
      <div className="w-[150px] h-[150px] bg-gray-200 rounded-md mx-auto my-auto"></div>

      {/* Right Side Image Above Bottom Section */}
      <div
        className="w-[120px] h-[115px] bg-gray-400 rounded-md absolute bottom-[154px] right-4"
        style={{ marginBottom: '10px' }}
      ></div>

      {/* Bottom Section */}
      <div className="w-full md:w-[509px] h-auto bg-white rounded-md p-4 flex flex-wrap gap-2">
        {/* Pill Components */}
        {[
          'Anti-Pollution + Sweat Resistant',
          'D-Tan Formula',
          'Non comedogenic',
          'Prevents premature aging',
        ].map((text, idx) => (
          <div
            key={idx}
            className="px-4 py-2 bg-gray-200 rounded-full text-[16px] md:text-[20px] font-medium leading-[16px] tracking-[0.5px] text-center"
          >
            {text}
          </div>
        ))}
      </div>
    </div>
  );
};

const SecondSection: React.FC = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const totalSlides = 3;

  const nextSlide = () => setCurrentSlide((currentSlide + 1) % totalSlides);
  const prevSlide = () =>
    setCurrentSlide((currentSlide - 1 + totalSlides) % totalSlides);

  return (
    <div className="w-full md:w-[764px] h-full bg-[#F8F8F8] rounded-[12px] relative overflow-hidden">
      {/* Slider Images */}
      <div
        className="w-full h-full flex transition-transform duration-300"
        style={{ transform: `translateX(-${currentSlide * 100}%)` }}
      >
        {[...Array(totalSlides)].map((_, index) => (
          <div
            key={index}
            className="w-full h-[300px] md:h-full flex-shrink-0 bg-gray-300 flex items-center justify-center"
          >
            <span className="text-xl font-bold">Image {index + 1}</span>
          </div>
        ))}
      </div>

      {/* Left & Right Circular Buttons */}
      <div className="absolute top-4 right-4 flex gap-2">
        <button
          onClick={prevSlide}
          className="w-[48px] h-[48px] bg-gray-400 rounded-full flex items-center justify-center"
        >
          <span className="text-black text-xl">←</span>
        </button>
        <button
          onClick={nextSlide}
          className="w-[48px] h-[48px] bg-gray-400 rounded-full flex items-center justify-center"
        >
          <span className="text-black text-xl">→</span>
        </button>
      </div>

      {/* Dots Indicator */}
      <div className="absolute bottom-4 left-4 flex gap-2">
        {[...Array(totalSlides)].map((_, index) => (
          <div
            key={index}
            className={`w-[12px] h-[12px] rounded-full ${
              currentSlide === index ? 'bg-black' : 'bg-gray-400'
            }`}
          ></div>
        ))}
      </div>
    </div>
  );
};

const TwoSectionLayout: React.FC = () => {
  return (
    <div className="flex flex-col md:flex-row gap-4 p-4">
      <FirstSection />
      <SecondSection />
    </div>
  );
};

export { FirstSection, SecondSection, TwoSectionLayout };
