'use client';
import React, { useState } from 'react';
import Image from 'next/image';

interface ProductComparisonSliderProps {
  beforeImage: string;
  afterImage: string;
  productIcon: string;
  productImage: string;
  productTitle: string;
}

const ProductComparisonSlider: React.FC<ProductComparisonSliderProps> = ({
  beforeImage,
  afterImage,
  productIcon,
  productImage,
  productTitle,
}) => {
  const [sliderPosition, setSliderPosition] = useState(50);

  const handleSliderChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSliderPosition(Number(e.target.value));
  };

  return (
    <div className="flex gap-[24px]">
      {/* Before and After Slider Section */}
      <div className="w-[764px] h-[632px] rounded-[12px] overflow-hidden relative">
        {/* After Image */}
        <Image
          src={afterImage}
          alt="After Image"
          layout="fill"
          objectFit="cover"
        />

        {/* Before Image with slider control */}
        <div
          className="absolute top-0 left-0 h-full overflow-hidden"
          style={{ width: `${sliderPosition}%` }}
        >
          <Image
            src={beforeImage}
            alt="Before Image"
            layout="fill"
            objectFit="cover"
          />
        </div>

        {/* Vertical Slider Line */}
        <div
          className="absolute top-0 h-full w-[2px] bg-white"
          style={{ left: `${sliderPosition}%` }}
        ></div>

        {/* Before and After Labels */}
        <div className="absolute top-4 left-4 text-white text-[20px] font-semibold">
          Before
        </div>
        <div className="absolute top-4 right-4 text-white text-[20px] font-semibold">
          After
        </div>

        {/* Slider Control */}
        <input
          type="range"
          min="0"
          max="100"
          value={sliderPosition}
          onChange={handleSliderChange}
          className="absolute bottom-4 left-1/2 transform -translate-x-1/2 w-[90%] cursor-pointer"
        />
      </div>

      {/* Product Info Section */}
      <div className="w-[539px] h-[632px] rounded-[12px] bg-[#F8F8F8] p-4 relative flex flex-col justify-between">
        {/* Product Icon */}
        <div className="w-[48px] h-[48px]">
          <Image
            src={productIcon}
            alt="Product Icon"
            width={48}
            height={48}
            className="object-contain"
          />
        </div>

        {/* Product Image (Centered) */}
        <div className="flex justify-center items-center flex-grow">
          <Image
            src={productImage}
            alt="Product"
            width={200}
            height={200}
            className="object-contain"
          />
        </div>

        {/* Product Title Section */}
        <div className="w-[507px] h-[130px] bg-white rounded-[12px] p-4 flex items-center justify-center">
          <h2 className="w-[253px] h-[54px] text-[36px] font-medium text-center">
            {productTitle}
          </h2>
        </div>
      </div>
    </div>
  );
};

export default ProductComparisonSlider;
